import {
    DOGDIC_SELECT,
    DOGDIC_CREATE
} from '../constants/actionTypes'

const initialState = (window.Cypress && window.initialState) ||
{
    "currentDic":"some dic",
    "dics": 
        [
            { 
                id:0, 
                name:"",
                data:
                    {
                        objName:" ",
                        dogName:" ",
                        zakazchikName: " ",
                        postavshikName: " ",
                        date: "", 
                        bank: "",
                        price: "",
                        status: "",
                        paid: "",
                        reponsible: "",
                        pdfScan: "   ",
                        commebt: ""
                    }                
            },
            { 
                id:1, 
                name:"+",
                data:
                    {
                        objName:"  +",
                        dogName:"  +",
                        zakazchikName: "  +",
                        postavshikName: "  +",
                        date: " +", 
                        bank: " +",
                        price: " +",
                        status: " +",
                        paid: " +",
                        reponsible: " +",
                        pdfScan: "    +",
                        commebt: " +"
                    }                
            }
        ]
};


export default function(state = initialState,action){
    switch (action.type){
        case DOGDIC_SELECT:
            return {...state,"currentDic":action.payload};
        case DOGDIC_CREATE:
            const out = {...state,
                dics:[...state.dics,
                    {id:(state.dics[state.dics.length-1].id)+1,
                     name:Object.entries(action.payload)[0][1],
                     data:action.payload
                    }
                ]
            }; 
            console.log("DOGDIC_SELECT out : ", out);
            return out;
        default:        
            return state;
    };
};

