
    import React from 'react';
    import font from '../css/Roboto_Mono/RobotoMono-Medium.ttf'
    import {
        Page,
        Text,
        View,
        Document,
        StyleSheet,
        PDFDownloadLink,
        Font,
    } from "@react-pdf/renderer";

    // Register font
    Font.register({
      family: 'Roboto Mono',
      src: font
    })

    const styles = StyleSheet.create({
        page: {
	        margin: "2em auto",
            fontFamily: "Roboto Mono",
            display: "flex",
            wordBreak: "break-word",
            border:"1px solid black",
	        hyphens: "auto"
        },
        section: {
            margin: 10,
            padding: 10,
        },
        content: {
            display: "block"
        }
    });

    let sampleDataOne = {type:
        {
            compFullName: " - ",
            compShortName: " - ",
            INN: " - ",
            KPP: " - ",
            OGRN: " - ",
            OKPO: " - ",
            GosRegDate: " - ",
            YurAdress: " - ",
            FactAdress: " - ",
            GenDirector: " - ",
            Buhgalter: " - ",
            tel: " - ",
            bankName: " - ",
            BIK: " - ",
            BillOne: " - ",
            BillTwo: " - ",
        }
    };
    let sampleDataTwo = {type:
        {
            Name: "-",
            FIO: "-",
            INN: "-",
            OGRNIP:"-",
            OKPO:"-",
            FactAdress:"-",
            bankName:"-",
            BIK:"-",
            BillOne:"-",
            BillTwo:"-"
        }
    };
    let sampleDataThree = {type:
        {
            NameInformal: "-",
            lastName: "-",
            firstName: "-",
            midName:"-",
            docType:"-",
            Serial:"-",
            number:"-",
            whoGave:"-",
            whenGave:"-",
            codeGave:"-",
            addressGave:"-"
        }
    };
    
    const templateOne = (_data = sampleDataOne.type) => {
        return (" ,         ,   ,    ''  " +
        _data.compFullName + " ,         , ()   ,    '', : " + 
        " : " + _data.compShortName  +
        " : " + _data.INN +
        " : " + _data.KPP +
        " : " + _data.OGRN + 
        " .: " + _data.YurAdress +
        " .: " + _data.FactAdress +
        " : " +  _data.bankName +
        " : " +  _data.BIK +
        " /: " +  _data.BillOne +
        " /: " +  _data.BillTwo +
        " ")
    };
    const templateTwo = (_data = sampleDataTwo.type) => {
        return(" ,      ,    ,   ,    ''  " + _data.Name + " ,     " + _data.FIO + ", ()   ,    '',  : : " + 
_data.Name + " : " + _data.INN + " : " + _data.OGRNIP + " .: " +  _data.FactAdress + " : " + _data.bankName +
" : " +  _data.BIK + " /: " + _data.BillOne + " /: " +  _data.BillTwo + "  " + _data.Name + " _/()/ " + _data.FIO + ""
        )
    };
    const templateThree = (_data = sampleDataTwo.type) => {
        return(",      ,    ,   ,    ''  " + _data.lastName + " " + _data.firstName + " " + _data.midName + " , " + _data.docType + "  " + _data.Serial + "  " +  _data.number + " ,  " +  _data.whoGave + " " +  _data.whenGave + "   " +  _data.codeGave + " , ()   '',  : : "  + _data.lastName + " " + _data.firstName + " " + _data.midName + " , " + _data.docType + "  " + _data.Serial + "  " +  _data.number + " ,  " +  _data.whoGave + " " +  _data.whenGave + "   " +  _data.codeGave + "  " + "_/()/"
        );
    };

    const zakazchik = {
        one:{ template: templateOne, data: sampleDataOne },
        two:{ template: templateTwo, data: sampleDataTwo },
        three:{ template: templateThree, data: sampleDataThree }
    };

    const data_to_text = (_data,_store) => {
        console.log("data_to_text on load : ", _store);
        const hold = Object.keys(JSON.parse(_data))[0];
        switch (hold) {
            case 'zakazchikTypeOneData':
                return JSON.stringify(zakazchik.one.template(JSON.parse(_data)[hold]));
            case 'zakazchikTypeTwoData':
                return JSON.stringify(zakazchik.two.template(JSON.parse(_data)[hold]));
            case 'zakazchikTypeThreeData':
                return JSON.stringify(zakazchik.three.template(JSON.parse(_data)[hold]));
            default:
                return "whatever";
        }
    };

    const PdfDocument = _props => (
        <Document>
            <Page style={styles.page}>
                <View style={styles.section}>
                    <Text style={styles.content}>
                        {_props.text}
                    </Text>
                </View>
            </Page>
        </Document>
    );

    const BeautyText = (_props) => (
      <div>
        <PdfDocument text={data_to_text(_props.text,_props.store)}/>
        <PDFDownloadLink 
            document={<PdfDocument 
                text={data_to_text(_props.text,_props.store)}/>
            } 
            fileName="somename.pdf"
        >
            {({ blob, url, loading, error }) => (loading ? 'Loading document...' : ' PDF     .')}
        </PDFDownloadLink>
      </div>
    );

    export default BeautyText;
