
//  IMPORTS ---------------------------------------------------------------------------------------

    const assert = require("./assert");
    
//   TASK -----------------------------------------------------------------------------------------

    /*
          {}  :
    + 1.  . {  {}{}  {--}}
    + 2.  . {  {}{}.  {--}}
    + 3.   {  {}{}.  {--}}
    + 4.   . {   {}{}  {--}}
    + 5.   {  {}{}.  {--}}

    +     {_}       1.
    + {}        {}.pdf  {}.doc
    */

//  INPUT DATA: DOGS data (DOGOVORS) -------------------------------------------------------------------

    const dogs = {
        " ":
            {
                "1999_1": {date:"1999/2/2", link:"link", name: "  19991.  1999/2/2"},
                "1999_2": {date:"1999/12/5", link:"link", name: "  1999A2.  1999/12/5"},
                "2000_1": {date:"2000/24/7", link:"link", name: "  2000A1.  2000/24/7"}
            },
        " ":
            {
                "1999_1": {date:"2/2/1999", link:"link", name: "  19991.  1999/2/2"},
                "1999_2": {date:"12/5/1999", link:"link", name: "  19992. o 1999/12/5"},
                "2019_2": {date:"24/7/2000", link:"link", name: "  20192.  2019/24/7"}
            },        
        " ":
            {
                "1999_1": {date:"2/2/1999", link:"link", name: "  19991.  1999/2/2"},
                "1999_2": {date:"12/5/1999", link:"link", name: "  19992.  1999/12/5"},
                "2000_3": {date:"24/7/2000", link:"link", name: "  19993.  2000/24/7"}
            },
        "  ":
            {
                "1999_1": {date:"2/2/1999", link:"link", name: "   19991.  1999/2/2"},
                "1999_2": {date:"12/5/1999", link:"link", name: "   19992.  1999/12/5"},
                "2000_4": {date:"24/7/2000", link:"link", name: "   20004.  2000/24/7"}
            },
        " ":
            {
                "1999_1": {date:"2/2/1999", link:"link", name: "  19991.  1999/2/2"},
                "1999_2": {date:"12/5/1999", link:"link", name: "  19992.  1999/12/5"},
                "2000_5": {date:"24/7/2000", link:"link", name: "  20005.  2000/24/7"}        
            }
    };

//  FUNCTIONS ----------------------------------------------------------------------------------------------------

    //  1 NAME EXTRACTOR ---------------------------------------------------------------------------------------------

    const nameExtractor = (_dogs=dogs) => {
        let out = [];
        Object.keys(dogs).map(dog => {for (let key in dogs[dog]) out.push(dogs[dog][key].name)});
        return out;
    };
    assert(
        "Name extractor extracts names",
        nameExtractor(),
        [
            "  1999A1.  1999/2/2",
            "  1999A2.  1999/12/5",
            "  2000A1.  2000/24/7",
            "  19991.  1999/2/2",
            "  19992.  1999/12/5",
            "  20192.  2019/24/7",
            "  19991.  1999/2/2",
            "  19992.  1999/12/5",
            "  19993.  2000/24/7",
            "   19991.  1999/2/2",
            "   19992.  1999/12/5",
            "   20004.  2000/24/7",
            "  19991.  1999/2/2",
            "  19992.  1999/12/5",
            "  20005.  2000/24/7"
        ]
    );

    //  2 LATEST DOGOVORTYPE NUMBER EXTRACTOR ------------------------------------------------------------------------------

    const latestExtractor = _dogName => {
        let keys = Object.keys(dogs[_dogName]); 
        let out = keys[keys.length-1].match(/(?<=\_)\d+/g)[0];
        return out;
    };
    assert(
        "IN: dogType & dogs pool, OUT: latest #num of required dogtype ",
        latestExtractor(" "),
        "1"
    );
    
    //  3 NEW DOGOVOR GENERATOR ---------------------------------------------------------------------------------------

    const sampleNy = { year:2020, month:1, day:1};
    
    const newDogGen = (_dogName,_date=sampleNy) => {
        const sampleDate = `${_date.year}/${_date.day}/${_date.month}`;   
        const out = _letter => {
            return ( (_date.month === 1 && _date.day === 1) && (latestExtractor(_dogName) === 1)
                ? `${_dogName} ${_date.year}${_letter}${+latestExtractor(_dogName)+1}. ot ${sampleDate}`
                : `${_dogName} ${_date.year}${_letter}1. ot ${sampleDate}` 
            );
        };
        switch(_dogName){
            case " ": return out("A");
            case " ": return out("");
            case " ": return out("");
            case "  ": return out("");
            case " ": return out("");
            default : return "who knows";
        };
        
    };
    assert(
            "IN: dog name, OUT: new dog name",
            newDogGen("nameOne",sampleNy),
            "nameOne 2020A2. ot 2020/1/1" // prev = "nameOne 2000 A1 2000/24/7"
        );
    assert(
        "IN: dog name, OUT: new dog name",
        newDogGen("nameTwo",sampleNy),
        "nameTwo 2020B1. ot 2020/1/1" //prev === "nameTwo 2019 B2 2019/24/7"
    );

//  EXPORTS ---------------------------------------------------------------------------------------------------------

    exports.newDogGen = newDogGen;
    exports.nameExtractor = nameExtractor;

